#gallerys{
    align-items: center;
    background-color: black;
    display: flex;
    flex-wrap: wrap;
    width:100vw;
    max-width:100vw;
    background-color:white;
}
.gallery{
    margin-left: 6rem;
    width:40vw;
}
.frameImg{
    height: auto;
    margin-top: 20px;
    max-width: 100%;
}
/* Media Query for screens smaller than 768px */
@media (max-width: 720px) {
    .gallery {
        width: 100%;
    }
}