.navbar{
    opacity:90%;
    background-color: rgb(30, 30, 30);
    border-bottom: 0.5px solid white;
    height:4rem;
    width:100vw;
    max-width:100%;
    display: flex;
    align-items: center;
    justify-content:space-between;
    position: sticky;
    top:0;
    margin: 0 auto;
    padding: 1rem 2rem;
    z-index:3;
}
.logo{
    object-fit: cover;
    height:4rem;
    width:10rem;
}
.desktopMenuListItem{
    font-size:15px;
    margin:2rem;
    cursor: pointer;

}
.desktopMenuListItem:hover{
    color:#FEE12B;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid #FEE12B;
}
.desktopMenuBtn{
    height: 2rem;
    cursor: pointer;
    background:#FEE12B;
    color: black;
    display: flex;
    border: none;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
}
.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
}
.active{
    color: #FEE12B;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid #FEE12B;
}
.mobMenu{
    display: none;
    object-fit: cover;
    height: 1.8rem;
}
.listItem{
    opacity:90%;
    background-color: rgb(30, 30, 30);
    cursor: pointer;
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
}

@media screen and (max-width:720px) {
    .mobMenu{
        display: flex;
    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display: none;
    }
}

