#certified{
    margin-top:-6rem;
    width: 100vw;
    max-width:80rem;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.certifiedImgs{
    background-color: white;
    margin-top:1.5rem;
    height:auto;
    width:100vw;
    max-width:90rem;
    
}
.certifiedImg{
    padding: auto;
    margin: 1rem;
    width:90vw;
    max-width: 100%;
}
.certifiedTitle{
    margin: 0.5rem;
    font-family:sans-serif;
}

/* Media query for mobile devices */
@media (max-width: 720px) {
    #certified {
        overflow: hidden;
        background-image: url(../../assets/bg.jpg);
        background-repeat:no-repeat;
        background-size: cover;
        margin-top:0rem;
    }
    .certifiedTitle {
        margin-top: 2rem;
        align-items: center;
        text-align: center;
        width: 100vw;
        font-size:20px;
    }
    .certifiedImgs {
        height: auto;
        max-width:100vw;
    }

    .certifiedImg {
        width: 60vw;
    }
}