#footermain{
    background-image: url(../../assets/bg.jpg);
    background-repeat:no-repeat;
    background-size:cover;
    margin-right: 2rem;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height:auto;
}
.LogoImg{
    margin-top:2rem;
    margin-left: 4rem;
}
.logoline1{
    color: rgb(143, 140, 140);
    margin-top: -0.7rem;
    margin-left: 2.6rem;
}
.logotext{
    color: rgb(143, 140, 140);
    margin-top: 0.5rem;
    margin-left: 1.8rem;
}
.logoline2{
    color: rgb(143, 140, 140);
    margin-top: -0.3rem;
    margin-left: 2.6rem;
}
.address{
    margin-top:2rem;
    color: rgb(143, 140, 140);
    margin-left: 7rem;

}
.addressText{
    list-style: none;
    padding-top:8px;
    color: rgb(143, 140, 140);
}
.list{
    list-style: none;
    margin-top:2rem;
    color: rgb(143, 140, 140);
    margin-left: 8rem;
}
li{
    padding:5px;
}
.copyright{
    color: rgb(143, 140, 140);
    margin-left: 9rem; 
    margin-top:3rem;
}

@media (max-width: 720px) {
    .LogoImg{
        height:3vh;
        width:9vw;
        margin-top:2rem;
        margin-left:2rem;
    }
    .logoline1{
        color: rgb(143, 140, 140);
        margin-top: -0.9rem;
        margin-left:0rem;
    }
    .logotext{
        font-size: 11px;
        color: rgb(143, 140, 140);
        margin-top: 0.5rem;
        margin-left:0.1rem;
    }
    .logoline2{
        color: rgb(143, 140, 140);
        margin-top: -0.3rem;
        margin-left:0rem;
    }
    .address{
        font-size: 12px;
        margin-top:2rem;
        color: rgb(143, 140, 140);
        margin-left:0rem;
    
    }
    .addressText{
        list-style: none;
        padding-top:3px;
        color: rgb(143, 140, 140);
    }
    .list{
        list-style: none;
        margin-top:2rem;
        color: rgb(143, 140, 140);
        margin-left:0rem;
        font-size: 12px;
    }
    li{
        padding:2.9px;
    }
    .copyright{
        font-size: 12px;
        color: rgb(143, 140, 140);
        margin-left:0rem; 
        margin-top:3rem;
    }
}





