#forms{
    height:38rem;
    background-color:white;
}
.formTitle{
    padding-top:1rem;
    color: black;
    top:5rem;
    width:100vw;
    font-size:2.5rem;
    font-weight:800;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
}
.formDesc{
    margin-top:0.5rem;
    color: rgb(139, 137, 137);
    align-items: center;
    text-align: center;
    font-size:1.1rem;
}
#form{
    padding-left: 10px;
    width: 100%;
    max-width: 100%;
    position: relative;
}
input{
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 3px;
    width:100%;
    font-family: montserrat;
    color: #2C3E50;
    font-size:13px;
}
.table1{
    margin-top:1rem;
}
.td1{
    font-size:13px;
    padding: 0 1rem;
    width:50vw;
}
.submitbtn{
    font-size:15.3px;
    width: 10vw;
    height: 2.3rem;
    cursor: pointer;
    background:#FEE12B;
    color: black;
    display: flex;
    border: none;
    border-radius:0.5rem;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
}
.td2{
    padding-left: 13rem;
}
.checkboxes{
    margin-left:1rem;
}
@media screen and (max-width: 720px) {
    .formTitle{
        padding-top:2rem;
        width:100vw;
        font-size:20px;
        font-weight:800;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        justify-content: center;
    }
    .formDesc{
        margin-top:0.5rem;
        color: rgb(139, 137, 137);
        align-items: center;
        text-align: center;
        font-size:13px;
    }
    #form{
        width: 100vw;
        position: relative;
    }
    input{
        padding:15px;
        border: 1px solid #ccc;
        width:100%;
        font-family: montserrat;
        color: #2C3E50;
        font-size:12px;
    }
    .table1{
        margin-top:1rem;
    }
    .td1{
        padding:3px 10px;
        width: 48vw;
    }
    .submitbtn{
        font-size:13px;
        width:16vw;
        height:2rem;
        background:#FEE12B;
        color: black;
    }
    .td2{
        padding-left:5px;
    }
    .checkboxes{
        font-size:14px;
    }
}