#clients{
    height:78vh;
    width: 100vw;
    overflow: hidden;
    background-image: url(../../assets/bg.jpg);
    background-repeat:no-repeat;
    background-size:cover;
}
.clientTitle{
    font-size:2rem;
    align-items: center;
    justify-content:center;
    text-align: center;
    margin-top:3rem;
}
.clientDiv{
    display: flex;
    flex-wrap: wrap;
    width: 100vw;
    height: 56vh;
}
.clientImg{
    margin: 1rem;
    height:50vh;
    width: 20vw;
}
.marquee {
    white-space: nowrap; /* Prevent line breaks */
    overflow: hidden; /* Hide overflowing content */
    animation: marquee 5s linear infinite; /* Adjust duration as needed */
  }
  
  .marquee img {
    margin-right: 20px; /* Adjust spacing between images */
  }
  
  @keyframes marquee {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(-100%);
    }
  }
@media (max-width: 720px) {
    #clients{
        height:auto;
        width: 100vw;
    }
    .clientTitle {
        font-size:20px;
    }

    .clientDiv {
        height: auto;
        width: 100%;
    }

    .clientImg {
        height: auto;
        width: 35vw; 
    }
}



