#marketings{
    height:110vh;
    width: 100vw;
    overflow: hidden;
    background-image: url(../../assets/bg.jpg);
    background-repeat:no-repeat;
    background-size:cover;
}
.marketingTitle{
    margin:3rem;
    font-size:2rem;
    font-weight:900;
    text-align: center;
    align-items: center;
    flex-direction:column;
    justify-content: center;
}
#mainBlock{
    margin-left:2rem;
    display: flex;
    flex-wrap: wrap;
}
.serviceBlock{
    cursor:pointer;
    align-items: center;
    display: flex;
    margin: 1rem;
    padding: 1rem 1rem;
    border-radius:6rem;
    background:rgb(40, 40, 40);
    opacity:75%;
}
.serviceBlockImg{
    margin-top:0.4rem;
    object-fit: cover;
    height: 4rem;
    width: 4rem;
    margin-left:1rem;
}
.serviceBlockText{
    margin-left:1rem;
    padding:1rem;
}
@media (max-width:720px) {
    #marketings{
        height:auto;
        width: 100vw;
    }
    .marketingTitle {
        margin-top:2rem;
        font-size:20px;
    }
    #mainBlock{
        padding: 0.5rem;
        margin-left:0rem;
        display: flex;
        flex-wrap: wrap;
    }
    .serviceBlock{
        margin:1rem;
        padding: 0rem 0rem;
        border-radius:10px;
    }
    .serviceBlockImg {
        margin-top:0rem;
        height:2rem;
        width: 2rem;
    }
    .serviceBlockText {
        font-size: 10px;
        margin-left:0;
    }
}