#slider{
    left: 0;
    height:15vh;
    background-color: white;
    overflow: hidden;
    width:100%;
    max-width:100rem;
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: center;
    text-align: center;
    z-index: -1;
}
.boxslider{
    width:100%;
    margin-left:3rem;
    display: flex;
    max-width:100rem;
}
.sliderImg{
  padding: 0 2rem;
    margin:1rem;
    display: inline-block;
    line-height: 100px;
}
.sliderTitle{
  text-align: center;
    margin-left:4rem;
    color: black;
    width:10rem;
}

.marquee {
  white-space: nowrap;
  overflow: hidden; 
  animation: marquee 1s linear infinite; 
}

.marquee img {
  margin-right: 20px;
}

@keyframes marquee {
  0% {
      transform: translateX(100%); 
  }
  100% {
      transform: translateX(-100%); 
  }
}

@media (max-width: 720px) {
  #slider{
      margin-top:-4rem;
      height:12vh;
      padding:5px;
  }

  .sliderImg {
    height:7vh;
  }

  .sliderTitle{
    margin-left: 2.2rem;
    font-size: 20px;
  }
}