#intro{
    height:110vh;
    width: 100vw;
    overflow: hidden;
    max-width: 100%;
    margin: 0 auto;
    position: relative;
    background-image: url(../../assets/bg.jpg);
    background-repeat:no-repeat;
    background-size: cover;
}
.bg{
    position: absolute;
    top:1rem;
    left: 0;
    z-index: 1;
    object-fit: cover;
    height:100vh;
}
.introContent{
    height:100vh;
    width:100vw;
    padding:2rem;
    font-size:3.5rem;
    font-weight:800;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top:4rem;
    margin-left:12rem;
}
.boxpara{
    font-weight:400;
    font-size:15px;
    margin-top:2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    
}
.btn{
    margin-top:3rem;
    margin-left:1rem;
    text-align: center;
    font-weight:800;
    font-size:large;
    height:10vh;
    width:23vw;
    cursor: pointer;
    background:#FEE12B;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius:3rem;
}

.color-change {
    font-size: 3.5rem;
    color: transparent;
    animation: gradientAnimation 4s infinite ;
    -webkit-background-clip: text;
    background-clip: text;
}
.text-animation {
    animation: changeText 4s infinite;
}

@keyframes changeText {
   
    25% {
        content: "Revenu...";
    }
    50% {
        content: "Customers...";
    }
    75% {
        content: "Sales...";
    }
    100%{
        content: "Money...";
    }
}

@keyframes gradientAnimation {
    0% {
        background-image: linear-gradient(to left, #f15ac4, #f53051);
    }
    100% {
        background-image: linear-gradient(to right, #30e0ec, #3f5feb);
    }
}

@media (max-width:720px) {
    #intro{
        z-index: -1;
        height:auto;
        display: flex;
    }
    .bg{
        display: none;
    }
    .introContent{
        padding-top: 2rem;
        margin-left:0rem;
        font-size: 2rem;
    }
    .color-change {
        font-size: 2.2rem;
    }
    .boxpara{
        margin-top:2.5rem;
        font-size:14px;
    }
    .btn{
        width:40vw;
        height: 2rem; /* Adjusted for mobile */
        padding: 0 0.5rem; /* Adjusted for mobile */
        margin-top:2rem;
        margin-left:1rem;
        text-align: center;
        font-weight:600;
        font-size:14px;
        cursor: pointer;
        background:#FEE12B;
        border: none;
        border-radius:0.2rem;
    }
}